<template>  
  <div class="row container">
    <div class="left">
      <div ref="viewer" class="view-container">
        <div class="pdfViewer"></div>
      </div>
    </div>          
    <div class="extracted-data right" v-if="extractedData.length > 0">
      <div class="result-title">共抽取<span class="extracted-count">{{wordNum}}</span>个字段</div>
      <div class="result-item" v-for="(item, index) in extractedData" :key="index">
        <div v-if="item.values.length!==0" class="item-collapse">
          <div class="item-header" @click="showToggle(item)">
            <span class="item-key">{{item.key}}</span>
            <img class="item-logo" v-if="item.show" src="./../../assets/icons/up.svg">
            <img class="item-logo" v-else src="./../../assets/icons/down.svg">
          </div>
          <div class="item-value" v-if="item.show">
            <p v-for="(wordItem, wordKey) in item.values"  :key="wordKey" @click="navigateTo(wordItem.pageNum, wordItem.location, index)">{{wordItem.value}}</p>
          </div>  
        </div>
        <div v-else class="item-disable">
          {{item.key}}
        </div> 
      </div>
    </div>
  </div>
</template>

<script>
import VueScrollTo from 'vue-scrollto';
import pdfjs from 'pdfjs-dist/webpack';

require('pdfjs-dist/web/pdf_viewer.css');
const pdfjsViewer = require('pdfjs-dist/web/pdf_viewer');

export default {
  name: 'pdf-structure-data',
  props: {
    extractedData: {
      type: Array
    },
    renderPdf: {

    }
  },
  computed: {
    wordNum() {
      const newArr = this.extractedData && this.extractedData.filter(item => {
        return item.values.length !== 0;
      });
      return newArr.length;
    }
  },
  data() {
    return {
      pdfViewer: null,
      pdfLinkService: null,
      loadingTask: null,
      pdfDoc: null
    };
  },
  methods: { 
    showToggle(item) {
      item.show = !item.show;
    },  
    navigateTo(pageNum, offset) {
      if (!pageNum && !offset) {
        return;
      }
      const pageHeight = 900;
      const page = this.$refs.viewer.querySelector(`[data-page-number="${pageNum}"]`);
      VueScrollTo.scrollTo(page, 500, {
        container: this.$refs.viewer,
        offset: Number(offset) * pageHeight,
        easing: [0.4, 0.0, 0.2, 1]
      });
    },
    setPdfPageScale() {
      this.pdfViewer.currentScale = 0.8;
    },
    viewPdf(base64Data) {
      this.pdfLinkService = new pdfjsViewer.PDFLinkService();
      this.pdfViewer = new pdfjsViewer.PDFViewer({
        container: this.$refs.viewer,
        linkService: this.pdfLinkService,
        removePageBorders: true
      });

      this.pdfLinkService.setViewer(this.pdfViewer);

      document.addEventListener('pagesinit', this.setPdfPageScale);
      this.loadingTask = pdfjs.getDocument({
        data: atob(base64Data),
        cMapUrl: '/cmaps/',
        cMapPacked: true
      });
      return this.loadingTask.promise.then(pdfDoc => {
        this.pdfDoc = pdfDoc;
        this.pdfViewer.setDocument(pdfDoc);
        this.pdfLinkService.setDocument(pdfDoc, null);
      });
    },
    destory() {
      if (this.pdfViewer) {
        this.pdfViewer.setDocument(null);
        this.pdfViewer.cleanup();
        this.pdfViewer = null;
      }
      if (this.pdfLinkService) {
        this.pdfLinkService.setDocument(null, null);
        this.pdfLinkService = null;
      }
      if (this.loadingTask) {
        this.loadingTask.destroy();
        this.loadingTask = null;
      }
      document.removeEventListener('pagesinit', this.setPdfPageScale);
    }
  },
  mounted() {    
    this.viewPdf(this.renderPdf);
  },
  beforeDestroy() {
    this.destory();
  }
};
</script>

<style lang="scss" scoped>
.container {
  height: 100%;
  overflow-x: hidden;
  .left {
    flex-shrink: 0;
    width: 730px;
    height: 100%;
    position: relative;
    .view-container {
      overflow: auto;
      position: absolute;
      width: 730px;
      height: 100%;
      /deep/ .canvasWrapper {
        border: solid 1px rgba(0, 0, 0, 0.1);
        border-radius: 4px;
        box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.1);
      }
    }
  }        
  .extracted-data {
    overflow: auto;
    flex-shrink: 0;
    margin-left: 24px;
    width: 450px;
    height: 100%;
    background-color: #ffffff;
    border-radius: 4px;
    border: solid 1px #e3edf1;

    .result-title {
      display: flex;
      align-items: center;
      height: 70px;
      margin: 8px 12px;
      padding-left: 16px;
      border-radius: 4px;
      background-image: linear-gradient(90deg, #2f79ed 0%, #305bd9 100%);
      color: #fff;

      .extracted-count {
        font-size: 36px;
        margin-left: 10px;
        margin-right: 10px;
      }
    }

    .item-disable {
      display: flex;
      align-items: center;
      height: 58px;
      padding-left: 16px;
      border-radius: 4px;
      border: solid 1px #efefef;
      background-color: #f8fbfc;
      font-size: 18px;
      color: #bac5ca;
    }

    .item-collapse {
      border-radius: 4px;
      border: solid 1px #d9eaff;
      background-color: #ffffff;
      &:hover {
        box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.08);
      }
    }

    .item-header {
      display: flex;
      cursor: pointer;
      align-items: center;
      justify-content: space-between;
      height: 58px;
      padding: 0 16px;
    }

    .item-value {
      padding-left: 28px;
      padding-bottom: 16px;
      font-size: 14px;
      color: #4a90e2;
      cursor: pointer;

      &:hover {
        color: #0070f3;
      }
    }   

    .item-logo {
      width: 12px;
      height: 12px;
    }

    .result-item {
      padding: 4px 12px;
      .item-key {
        font-size: 18px;
      }                    
    }
  }
}
</style>

<style lang="scss">
.pdfViewer {
  .popupAnnotation {
    display: none !important;
  }
}
</style>
