<template>
  <div class="file-header">
    <span class="file-group">
      <img class="file-logo" v-if="this.filename.toLowerCase().indexOf('.pdf')!==-1" src="./../../assets/icons/pdf.svg" />
      <img class="file-logo" v-else src="./../../assets/icons/doc.svg">
      <span class="file-name">{{this.displayFilename}}</span>
    </span>
    <span class="file-type-title" v-if="type">报告类型:</span>
    <div class="file-type" v-if="type">{{this.type}}</div>
    <div class="file-change">
      <file-change @change="onPdfFileChange" class="file"></file-change>
    </div>
  </div>
</template>

<script>
import FileChange from '@/components/common/FileChange';

export default {
  name: 'file-header',
  props: {
    filename: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      displayFilename: ''
    };
  },
  methods: {
    onPdfFileChange(file) {
      this.displayFilename = file.name.substr(0, file.name.length - 4);
      this.$emit('change', file);
    }
  },
  mounted() {
    this.displayFilename = this.filename.substr(0, this.filename.length - 4);
  },
  components: {
    FileChange
  }
};
</script>

<style lang="scss" scoped>
.file-header { 
  display: flex;
  height: 40px;
  width: 100%;
  align-items: center;
  background-color: #dde5f0;
  .file-logo {
    width: 18px;
    height: 18px;
    margin-right: 9px;
  }
  .file-name {
    font-family: PingFangSC-Semibold;
    font-size: 16px;
    color: #333333;
  }
  .file-type-title {
    margin-left: 40px;
    margin-right: 8px;
    font-size: 12px;
    color: #5b6b71;
  }
  .file-type {
    padding: 3px 8px;
    font-size: 12px;
    color: #5b6b71;
    background-color: #d4e1e6;
  }
  .file-change {
    margin-left: 40px;
  }
}
</style>
