<template>
  <div class="dropbox" :class="{dragin: isGragIn}">
    <div class="icon-box">
      <div class="icon-pdf"></div>
    </div>
    <div class="upload-text-box">
      <p class="upload-text">请选择文件，或将其拖入框中</p>
    </div>
    <div class="upload-click-choose">
      <button @click="chooseFile">选择文件</button>
      <input type="file" ref="file" class="filebox" @change="onFileChange" :accept="fileFormat">
    </div>
  </div>
</template>
<script>
import { Message } from 'element-ui';

const message = Message;
export default {
  props: {
    fileFormat: {
      type: String,
      default: '.pdf,.doc,.docx'
    }
  },
  data() {
    return {
      files: [],
      isGragIn: false
    };
  },
  methods: {
    onDrag(ev) {
      ev.stopPropagation();
      ev.preventDefault();
      this.isGragIn = true;
    },
    chooseFile() {
      this.$refs.file.click();
    },
    onDrop(ev) {
      ev.stopPropagation();
      ev.preventDefault();
      const file = ev.dataTransfer.files[0];
      const arr = file.name.split('.');
      const fileType = arr[arr.length - 1] || '';
      const left = '请选择';
      const right = '格式的文件！';
      const fileTypeStr = `${left}${this.fileFormat}${right}`;
      if (file && (this.fileFormat && this.fileFormat.indexOf(fileType.toLowerCase()) < 0)) {
        message({
          showClose: true,
          message: fileTypeStr,
          type: 'error'
        });
        this.isGragIn = false;
      } else if (file) {
        this.$emit('change', file);
      }
    },
    onDragLeave(e) {
      e.stopPropagation();
      e.preventDefault();
      this.isGragIn = false;
    },
    onFileChange() {
      const file = this.$refs.file.files[0];
      const arr = file.name.split('.');
      const fileType = arr[arr.length - 1] || '';
      const left = '请选择';
      const right = '格式的文件！';
      const fileTypeStr = `${left}${this.fileFormat}${right}`;
      if (file && (this.fileFormat && this.fileFormat.indexOf(fileType.toLowerCase()) < 0)) {
        message({
          showClose: true,
          message: fileTypeStr,
          type: 'error'
        });
        this.isGragIn = false;
      } else if (file) {
        this.$emit('change', file);
      }
    }
  },
  mounted() {
    const dropbox = this.$el;
    dropbox.addEventListener('dragenter', this.onDrag, false);
    dropbox.addEventListener('dragover', this.onDrag, false);
    dropbox.addEventListener('dragleave', this.onDragLeave, false);
    dropbox.addEventListener('drop', this.onDrop, false);
  }
};
</script>
<style lang="scss" scoped>
.dropbox {
  width: 222px;
  height: 312px;
  background-color: rgba(0, 0, 0, 0.06);
  border-radius: 8px;
  border: dashed 2px #4db9ff;
  &:hover {
    border: dashed 2px #92f8ff;
  }
  &:active{
    border: dashed 2px #32aeff;
  }
  .icon-box {
    height: 186px;
    display: flex;
    justify-content: center;
    align-items: center;
    .icon-pdf {
      height: 81px;
      width: 60px;
      background: url("../../assets/icons/drag-pdficon.svg");
    }
  }
  .upload-text-box {
    margin-top: -10px;
    .upload-text {
      width: 128px;
      height: 48px;
      margin: 0 auto;
      padding: 0;
      text-align: center;
      font-size: 16px;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 0px;
      color: #fff;
    }
  }
  .upload-click-choose {
    margin-top: 25px;
    text-align: center;
    button {
      position: relative;
      width: 88px;
      height: 28px;
      background-color: #fafcfd;
      border-radius: 4px;
      font-size: 14px;
      color: #2b50bb;
      outline:none;
      border:none;
      cursor: pointer;
       &:hover {
        background-color: #ecf5ff;
      }
    }
    .filebox {
      position: absolute;
      width: 0;
      height: 0;
      left: 0;
      visibility: hidden;
      top: 0px;
      opacity: 1;
      cursor: pointer;
    }
  }
}
.dragin {
  border: 1px solid #ddd;
}
</style>
