import axios from 'axios';

export default class resourceWrapper {
  static ocrFile(file1) {
    const formData = new FormData();
    formData.append('file', file1);
    // formData.append('mode', 'presentation');
    return axios
      .post('/api/ocr', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(
        res => {
          return res.data || [];
        },
        (err) => {
          return Promise.reject(err);
        }
      );
  }
  static analyzingPdfFile(file1) {
    const formData = new FormData();
    formData.append('file', file1);
    formData.append('mode', 'presentation');
    return axios
      .post('/api/pdf_content_extraction', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(
        res => {
          return res.data || [];
        },
        (err) => {
          return Promise.reject(err);
        }
      );
  }
  static analyzingContractPdfFile(file1) {
    const formData = new FormData();
    formData.append('file', file1);
    formData.append('mode', 'presentation');
    return axios
      .post('/api/pdf_insurance', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(
        res => {
          console.log(res);
          return res.data || [];
        },
        (err) => {
          return Promise.reject(err);
        }
      );
  }
  static extractPdfStructureData(file, type) {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('type', type);
    return axios.post('/api/structure_extraction',
      formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(res => {
        const data = res.data || {};
        const result = {};
        if (data) {
          result.extractedData = data.data;
        }
        result.highlightedPdfFile = data.pdf;
        return result;
      }, (err) => {
        return Promise.reject(err);
      })
      .catch(() => {
        return {};
      });
  }
  static sentimentAnalysisData(newsTitle, newsContent) {
    return axios.post('/api/news_analytics', { title: newsTitle, content: newsContent })
      .then(
        res => {
          return res.data || [];
        },
        (err) => {
          return Promise.reject(err);
        }
      );
  }
  static newsAnalysisData(newsContent) {
    return axios.post('/api/news_questions', { text: newsContent }).then(res => {
      return res.data || [];
    }, (err) => {
      return Promise.reject(err);
    });
  }
}
