import Vue from 'vue';
import Router from 'vue-router';

import authService from '@/services/authService';
import NProgress from 'nprogress';
// import HomePage from '@/views/HomePage';
import PageTemplate from '@/views/file-processing-pages/PageTemplate';
import PdfExtractionPage from '@/views/file-processing-pages/PdfExtractionPage';
// import OcrRecognition from '@/views/file-processing-pages/OcrPage';
// import PdfAnalyzing from '@/views/file-processing-pages/PdfAnalyzingPage';
// import PdfComparison from '@/views/file-processing-pages/PdfComparisonPage';
// import SentimentAnalysis from '@/views/file-processing-pages/SentimentAnalysis';
// import NewsAnalyzing from '@/views/file-processing-pages/NewsAnalyzing';


// webpackPrefetch feature need webpack 4.6.0, see details at https://webpack.js.org/guides/code-splitting/

const LoginPage = () => import(/* webpackPrefetch: true, webpackChunkName: "login-page" */ '@/views/LoginPage');
const NotFoundPage = () => import(/* webpackPrefetch: true, webpackChunkName: "not-found-page" */ '@/views/NotFoundPage');

Vue.use(Router);

const router = new Router({
  // mode: 'history',
  routes: [
    // {
    //   path: '/',
    //   name: 'home-page',
    //   component: HomePage,
    //   meta: { requiresAuth: true }
    // },
    {
      path: '/',
      component: PageTemplate,
      meta: {
        requiresAuth: true
      },
      children: [
        {
          path: '',
          name: 'extraction',
          component: PdfExtractionPage
        }
        // {
        //   path: 'ocr-recognition',
        //   name: 'ocr-recognition',
        //   component: OcrRecognition
        // },
        // {
        //   path: 'analyzing',
        //   name: 'analyzing',
        //   component: PdfAnalyzing
        // },
        // {
        //   path: 'comparison',
        //   name: 'comparison',
        //   component: PdfComparison
        // },
        // {
        //   path: 'contract-analyzing',
        //   name: 'contract-analyzing',
        //   component: PdfAnalyzing
        // },
        // {
        //   path: 'sentiment-analysis',
        //   name: 'sentiment-analysis',
        //   component: SentimentAnalysis
        // },
        // {
        //   path: 'news-analyzing',
        //   name: 'news-analyzing',
        //   component: NewsAnalyzing
        // }
      ]
    },
    {
      path: '/login',
      name: 'login-page',
      component: LoginPage
    },
    {
      path: '/*',
      name: 'not-found-page',
      component: NotFoundPage
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    if (!authService.isLoggedIn()) {
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      });
    } else {
      NProgress.start();
      next();
    }
  } else {
    NProgress.start();
    next(); // 确保一定要调用 next()
  }
});
router.afterEach(() => {
  NProgress.done();
});

export default router;
