<template>
  <div class="file-upload">
    <input type="file" :id="uniqueId" @change="onFileChange" ref="file" class="inputfile" :accept="fileFormat"/>
    <label :for="uniqueId" :class="{'has-file': hasFile}" class="row" ref="label" >
      <div class="text">{{text}}</div>
    </label>
  </div>
</template>

<script>
import StringHelper from '@/helpers/stringHelper';
import { Message } from 'element-ui';

const message = Message;

export default {
  name: 'file-change',
  props: {
    text: {
      type: String,
      default: '更改文件'
    },
    changeFile: {
      type: Boolean,
      default: false
    },
    fileFormat: {
      type: String,
      default: '.pdf,.doc,.docx'
    }
    
  },
  data() {
    return {
      fileName: '更改文件',
      uniqueId: StringHelper.getUniqueId('file')
    };
  },
  computed: {
    hasFile() {
      return this.fileName !== '更改文件';
    }
  },
  methods: {
    onFileChange() {
      const file = this.$refs.file.files[0];
      this.fileName = file ? file.name : '上传文件';
      const left = '请选择';
      const right = '格式的文件！';
      const fileTypeStr = `${left}${this.fileFormat}${right}`;
      const arr = file.name.split('.');
      const fileType = arr[arr.length - 1] || '';
      if (file && (this.fileFormat && this.fileFormat.indexOf(fileType.toLowerCase()) < 0)) {
        message({
          showClose: true,
          message: fileTypeStr,
          type: 'error'
        });
      } else if (file) {
        this.$emit('change', file);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../scss/color.scss";

.file-upload {
  .inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
    & + label {
      overflow: hidden;
      width: 72px;
      height: 24px;
      background-color: #ffffff;
      border-radius: 2px;
      opacity: 0.8;
      padding: 8px 42px;
      font-size: 16px;
      color: #f1e5e6;
      text-overflow: ellipsis;
      white-space: nowrap;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      .icon {
        svg {
          width: 24px;
          height: 24px;
          fill: #f1e5e6;
        }
      }

      .text {
        font-size: 12px;
        color: #3b75ce;
      }
    }
  }

  .inputfile:focus + label,
  .inputfile.has-focus + label,
  .inputfile + label:hover {
    border: solid 1px #bcd6e8;
    background-color: #fff;
    opacity: 1;
  }

  .inputfile + label:active {
    border: solid 1px #bcd6e8;
    background-color: rgba(250, 252, 253, 0.4);
  }
}
</style>

