<template>
  <div
    class="pdf-extraction-page"
    v-loading="isLoading"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <div class="pdf-extraction-page_container" v-if="isInitPage">
      <div class="pdf-extraction-page_container_banner">
        <div class="pdf-extraction-page_container_banner_bg"></div>
        <file-upload v-if="this.file===null" @change="onFileChange" class="file"></file-upload>
        <div v-else class="pdf-extraction-page_container_banner_content row">
          <div class="preview" v-if="fileType.toLowerCase().indexOf('pdf')!==-1">
            <canvas id="the-canvas" class="canvas"></canvas>
          </div>
          <div class="preview-info">
            <file-header
              class="top-header"
              :filename="fileName"
              :type="uploadFileType"
              @change="onFileChange"
            ></file-header>
          </div>
        </div>
        <div class="file-type_group">
          <el-select class="file-type_group_select" v-model="uploadFileType" placeholder="请选择文件类型">
            <el-option
              v-for="(item,index) in uploadFileTypeList"
              :key="item+index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
          <button
            :class="{'btn-gray':this.file===null}"
            :disabled="isDisable"
            @click="startExtraction"
          >开始解析</button>
        </div>
      </div>
      <div class="pdf-extraction-page_container_footer row">
        <div class="pdf-extraction-page_container_footer_left">
          <div class="title">
            香侬科技文档处理系统
            <br>
            <span>Pdf Retrieval and Information Mining Engine</span>
          </div>
        </div>
        <div class="pdf-extraction-page_container_footer_right">
          <div class="title">PDF抽取</div>
          <div
            class="content"
          >根据用户对字段的定义，自动抽取目标PDF文件中的结构化字段信息。香侬科技首先使用自研的PDF解析系统，将PDF文件解析为抽取可用的标准格式。再通过基于机器阅读理解（MRC）、命名实体识别（NER）、关系抽取（RE）等的深度学习算法模型对PDF文档进行深度语义理解，并根据客户需要进行重要结构化信息的抽取</div>
        </div>
      </div>
    </div>
    <div class="pdf-extraction-res" ref="container" v-else>
      <div class="pdf-extraction-res_header">
        <div class="goBack" @click="goBack">后退</div>
        <file-header
          class="top-header"
          :filename="fileName"
          :type="uploadFileType"
          @change="onModifyFileChange"
        ></file-header>
      </div>
      <div class="pdf-extraction-res_container row">
        <pdf-structure-data
          v-if="fileName&& !rerender"
          :extractedData="extractedData"
          :renderPdf="renderPdf"
        ></pdf-structure-data>
      </div>
    </div>
  </div>
</template>

<script>
import FileUpload from '@/components/common/FileUpload';
import FileHeader from '@/components/common/FileHeader';
import PdfStructureData from '@/components/common/PdfStructureData';
import resourceWrapper from '@/rest/resourceWrapper';
import PDFJS from 'pdfjs-dist';

export default {
  name: 'pdf-extraction',
  data() {
    return {
      file: null,
      fileName: '',
      rerender: false,
      extractedData: [],
      isDisable: true,
      renderPdf: '',
      uploadFileTypeList: [
        '发行办法',
        '发行公告——同业存单',
        '发行公告——金融债券',
        '募集说明书',
        '付息公告',
        '发行结果公告',
        '发行情况公告-同业存单',
        '发行情况公告-超短融资券',
        '发行情况公告-非公开发行债券',
        '发行情况公告-短期融资券',
        '发行情况公告-其他债券',
        '发行有关事宜通知',
        '票面利率重置的公告/利率调整公告',
        '赎回/回售公告',
        '发行章程',
        '兑付公告',
        '债券交易流通要素公告',
        '同业存单流通要素公告'
      ],
      uploadFileType: '',
      fileType: '',
      fileUrl: '',
      isLoading: false,
      isInitPage: true,
      activeFileTypeIndex: 0,
      pdfType: '',
      previewPdfTask: null
    };
  },
  methods: {
    startExtraction() {
      this.extractPdf(this.file);
    },
    onFileChange(file) {
      this.file = file;
      this.fileUrl = URL.createObjectURL(file);
      this.fileName = file.name;
      if (this.file) {
        this.isDisable = false;
      }
      const arr = file.name.split('.');
      this.fileType = arr[arr.length - 1] || '';
      if (this.fileType.toLowerCase().indexOf('pdf') !== -1) {
        this.previewPdf(this.fileUrl);
      }
    },
    onModifyFileChange(file) {
      this.destory();
      this.file = file;
      this.fileName = file.name;
      const arr = file.name.split('.');
      this.fileType = arr[arr.length - 1] || '';
      this.rerender = true;
      this.extractPdf(this.file);
    },
    extractPdf(file) {
      this.isLoading = true;
      this.extractedData = [];
      this.renderPdf = '';
      return resourceWrapper
        .extractPdfStructureData(file, this.uploadFileType)
        .then(data => {
          this.isLoading = false;
          this.isInitPage = false;
          this.extractedData = data.extractedData.map(item => {
            if (item.values.length !== 0) {
              item.show = false;
            }
            return item;
          });
          this.renderPdf = data.highlightedPdfFile;
          this.rerender = false;
        }, err => {
          if (err.response) {
            this.$message.error('请求服务器错误');
          }
        });
    },
    previewPdf(fileUrl) {
      this.previewPdfTask = PDFJS.getDocument({
        url: fileUrl,
        cMapUrl: '/cmaps/',
        cMapPacked: true
      });
      return this.previewPdfTask.then(pdf => {
        pdf.getPage(1).then(page => {
          const scale = 1.5;
          const viewport = page.getViewport(scale);
          const canvas = document.getElementById('the-canvas');
          const context = canvas.getContext('2d');
          canvas.height = viewport.height;
          canvas.width = viewport.width;
          const renderContext = {
            canvasContext: context,
            viewport
          };
          page.render(renderContext);
        });
      });
    },
    goBack() {
      this.file = null;
      this.isInitPage = true;
    },
    destory() {
      if (this.previewPdfTask) {
        this.previewPdfTask.destroy();
        this.previewPdfTask = null;
      }
      if (this.fileUrl) {
        window.URL.revokeObjectURL(this.fileUrl);
      }
    }
  },
  beforeDestroy() {
    this.destory();
  },
  components: {
    FileUpload,
    FileHeader,
    PdfStructureData
  }
};
</script>
<style lang="scss" scoped>
.pdf-extraction-page {
  width: 100%;
  height: 100%;
  &_container {
    width: 100%;
    height: 100%;
    &_banner {
      position: relative;
      display: flex;
      height: 426px;
      width: 100%;
      background: linear-gradient(0deg, #569fdf 0%, #2b50bb 100%);
      align-items: center;
      justify-content: center;
      z-index: 0;
      &_bg {
        position: absolute;
        top: 60px;
        right: 0px;
        width: 580px;
        height: 366px;
        background: url("./../../assets/icons/fileuploadbg.svg") no-repeat;
        background-size: cover;
        opacity: 0.4;
        z-index: -1;
      }
      &_content {
        position: relative;
        .preview {
          width: 222px;
          height: 313px;
          background-color: #ffffff;
          box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.04);
          border-radius: 4px;
          .canvas {
            width: 222px;
            height: 313px;
          }
        }

        .preview-info {
          display: none;
        }
        &:hover {
          .preview-info {
            display: block;
            position: absolute;
            width: 222px;
            height: 313px;
            /deep/ .file-header {
              display: flex;
              flex-direction: column;
              justify-content: space-around;
              padding: 24px;
              background-color: #eef2f5;
              align-items: center;
              width: 222px;
              height: 313px;
              .file-group{
                display: flex;
                width: 174px;
              }
              .file-type-title {
                display: none;
              }
              .file-type {
                display: none;
              }
              .file-name{
                display: inline-block;
                width: 150px;
                word-wrap:break-word; 
              }
              .file-change {
                margin-left: 0;
                .file-upload {
                  background-color: #eef2f5;
                  .inputfile {
                    & + label {
                      background-color: #eef2f5;
                      color: #2b50bb;
                      &:hover {
                        border: none;
                      }
                    }
                  }
                }
              }
            }
          }
        }
        .btn-group {
          &_title {
            margin-left: 40px;
            margin-top: 88px;
            font-size: 20px;
            color: #fff;
          }
          &_filetype {
            margin-top: 12px;
            margin-left: 30px;
            &_item {
              padding: 7px 8px;
              background-color: rgba(0, 0, 0, 0.08);
              border-radius: 2px;
              font-size: 12px;
              margin-left: 10px;
              text-align: center;
              border: solid 1px rgba(255, 255, 255, 0.1);
              color: rgba(255, 255, 255, 0.3);
              cursor: pointer;
            }
            .active {
              border: solid 1px #ffffff;
              color: #fff;
            }
          }
        }
        button {
          width: 144px;
          height: 40px;
          margin-left: 40px;
          margin-top: 40px;
          align-self: center;
          line-height: 40px;
          text-align: center;
          font-size: 14px;
          background-image: linear-gradient(180deg, #6dc3f5 0%, #53b1f1 100%);
          border-radius: 4px;
          color: #fff;
          outline: none;
          border: none;
          cursor: pointer;
          &:hover {
            background-image: linear-gradient(180deg, #8dd4f7 0%, #6dc8f5 100%);
          }
          &:active {
            background-image: linear-gradient(180deg, #6dc3f5 0%, #53b1f1 100%);
          }
        }
      }
      .file-type_group {
        display: flex;
        flex-direction: column;
        /deep/ .file-type_group_select {
          margin-left: 40px;
          /deep/ .el-input {
            .el-input__inner {
              font-size: 16px;
              color: #fff;
              height: 38px;
              border-radius: 2px;
              border: solid 1px #ffffff;
              background-color: transparent;
              &::-webkit-input-placeholder {
                color: #fff;
              }
              &::-moz-placeholder {
                /* Mozilla Firefox 19+ */
                color: #fff;
              }
              &:-moz-placeholder {
                /* Mozilla Firefox 4 to 18 */
                color: #fff;
              }
              &:-ms-input-placeholder {
                /* Internet Explorer 10-11 */
                color: #fff;
              }
            }
            .el-select__caret {
              color: #fff;
            }
          }
        }

        button {
          width: 144px;
          height: 40px;
          margin-top: 40px;
          margin-left: 40px;
          line-height: 40px;
          text-align: center;
          font-size: 14px;
          background-image: linear-gradient(180deg, #6dc3f5 0%, #53b1f1 100%);
          border-radius: 4px;
          color: #fff;
          outline: none;
          border: none;
          cursor: pointer;
          &:hover {
            background-image: linear-gradient(180deg, #8dd4f7 0%, #6dc8f5 100%);
          }
          &:active {
            background-image: linear-gradient(180deg, #6dc3f5 0%, #53b1f1 100%);
          }
        }
        .btn-gray {
          background-image: none;
          background-color: #bac3cc;
          cursor: not-allowed;
          &:hover {
            background-image: none;
          }
          &:active {
            background-image: none;
          }
        }
      }
    }
    &_footer {
      width: 100%;
      height: calc(100% - 490px);
      &_left {
        position: relative;
        width: 550px;
        height: 100%;
        background: url("./../../assets/icons/primetext.svg") no-repeat;
        background-size: 320px 86px;
        background-position: 196px 45px;
        .title {
          position: absolute;
          top: 78px;
          left: 198px;
          font-size: 24px;
          color: #333;
          letter-spacing: 2.7px;
          span {
            font-size: 14px;
            letter-spacing: 0px;
            color: #666;
          }
        }
      }
      &_right {
        width: calc(100% - 550px);
        padding-top: 56px;
        .title {
          padding-left: 16px;
          font-size: 18px;
          color: #2b50bb;
        }
        .content {
          padding: 16px;
          font-size: 14px;
          color: #666;
        }
      }
    }
  }
}
.pdf-extraction-res {
  background-color: #f1f6f8;
  height: 100%;
  padding-bottom: 20px;
  &_header {
    display: flex;
    position: relative;
    height: 40px;
    width: 100%;
    padding-left: 86px;
    align-items: center;
    background-color: #dde5f0;
    .goBack {
      position: absolute;
      left: 20px;
      background: #fff;
      padding: 2px 8px;
      border-radius: 4px;
      font-size: 12px;
      color: #2b50bb;
      cursor: pointer;
    }
  }
  &_container {
    overflow: auto;
    margin-top: 20px;
    height: calc(100% - 40px);
    justify-content: center;
  }
}
</style>
<style lang="scss">
.el-select-dropdown {
  .el-scrollbar {
    .el-select-dropdown__wrap {
      .el-scrollbar__view {
        .el-select-dropdown__item {
          color: #666666;
          font-size: 16px;
          height: 36px;
          line-height: 36px;
        }
        .el-select-dropdown__item.selected {
          color: #666666;
          font-size: 16px;
          background-color: #e8f1f8;
          font-family: "PingFangSC-Semibold";
        }
      }
    }
  }
}
</style>


